/* properties for whole page */
* {
  box-sizing: border-box;
  transition: all 0.5s ease-in;
}

/* add line-hight and background color for body */
body {
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  line-height: 1.3;
  background-color: darkblue;
}

/* linear-gradient background for header and other header formatting */
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1%;
  background: linear-gradient(0.25turn, rgb(3, 3, 41), rgb(2, 39, 2), black);
  color: #fff;
  width: 100%;
  height: fit-content;
}

/* tital font and color */
.title {
  font-size: 250%;
  color: gray;
}

/* set text size, color, and other properties of heading and paragraph elements */
h2 {
  color: gray;
  font-size: 150%;
}

h4 {
  text-decoration: underline;
  font-size: 200%;
  padding: 10px;
  color: #fff;
  padding: 10px;
}

h5 {
  font-size: 150%;
  background-color: rgb(34, 5, 5);
  opacity: 0.9;
  text-align: center;
  padding: 10px;
}

p {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2%;
  margin: 1%;
  text-indent: 50px;
}

/* styling for profile picture */
#pic {
  height: 200px;
  width: 166px;
  border-style: solid;
  border-color: rgb(0, 110, 255);
  border-width: 1px;
  border-radius: 50px;
  padding: 1px;
  box-shadow: 10px 10px 5px black;
  margin: 20px;
}

/* contact info section */
#info {
  color: gray;
  min-width: 300px;
  flex-direction: row;
  padding: 10px;
}

#info a {
  color: #b9c6ae;
  font-weight: bold;
  text-decoration: none;
}

#info a:hover {
  background-size: 100% 100%;
  background-color: rgb(42, 126, 165);
}

#info li {
  justify-content: space-between;
  text-align: center;
  display: inline-block;
  padding: 5px;
  background: black;
  min-width: 525px;
}

#proficiencies {
  color: white;
  display: inline-block;
  padding: 5px;
  background-color: rgb(41, 66, 45);
}

#proficiencies li {
  padding: 2px;
}

/* Navigation bar section */
nav {
  display: flex;
  background-color: #7fd892;
  overflow: hidden;
}

nav a {
  color: #13293d;
  padding: 14px 20px;
  text-decoration: none;
  text-align: center;
}

nav a:hover {
  background-size: 100% 100%;
  background-color: rgb(42, 126, 165);
}

/* Dropdown button */
.dropdown .dropbtn {
  font-size: 35px;
  border: 2px;
  outline: none;
  background-color: #7fd892;
  font-family: inherit;
  margin: 0;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
}

/* Dropdown content (hidden by default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-content a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

/* Add a grey background color to dropdown links on hover */
.dropdown-content a:hover {
  background-color: rgb(42, 126, 165);
  background-size: 100% 100%;
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}

/* style for cards */
.card {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2%;
  justify-content: center;
}

#aboutme,
.card,
#applications h4 {
  color: bisque;
}

.card img {
  height: 200px;
  width: 400px;
  border-style: solid;
  border-color: rgb(0, 110, 255);
  border-width: 1px;
  border-radius: 50px;
  padding: 1px;
  box-shadow: 10px 10px 5px black;
  margin: 20px;
  filter: grayscale(50%);
}

/* Make first work sample image larger */
#prework img {
  height: 250px;
  width: 500px;
}

.card img:hover {
  filter: grayscale(0%);
  transform: scale(1.1);
  border-color: chartreuse;
  border-width: 3px;
}

.form {
  margin: 20px;
}

.form input {
  display: block;
  margin-top: 5px;
  margin-bottom: 5px;
  width: 25%;
  min-width: 400px;
}

#error-text {
  color: red;
}

#message {
  height: 100px;
}

/* Screen size section */
@media screen and (max-width: 1000px) {
  main,
  nav {
    flex-direction: column;
    max-width: fit-content;
  }

  #info {
    align-items: flex-start;
    top: 1660px;
    left: 2px;
    transition: none 0s;
  }

  .dropdown .dropbtn {
    text-align: center;
    align-items: center;
    display: block;
    align-items: center;
  }

  footer {
    height: 300px;
  }
}

@media screen and (max-width: 896px) {
  main,
  nav {
    flex-direction: column;
    max-width: fit-content;
  }

  #info {
    align-items: flex-start;
    top: 2440px;
    left: 2px;
    transition: none 0s;
  }

  .dropdown .dropbtn {
    text-align: center;
    align-items: center;
    display: block;
    align-items: center;
  }

  footer {
    height: 340px;
  }
}

/* fix animation when resizing */
@media screen and (min-width: 1000px) {
  #info {
    transition: none 0s;
  }
}
